import React, {useState} from 'react'
import {homeWorkReducer} from './bll/homeWorkReducer'
import s from '../../p1-main/m1-ui/u1-app/App.module.css'

export type UserType = { _id: number, name: string, age: number }

const initialPeople = [
    {_id: 0, name: 'Кот', age: 3},
    {_id: 1, name: 'Александр', age: 66},
    {_id: 2, name: 'Коля', age: 16},
    {_id: 3, name: 'Виктор', age: 44},
    {_id: 4, name: 'Дмитрий', age: 40},
    {_id: 5, name: 'Ирина', age: 55},
]

function HW8() {
    const [people, setPeople] = useState<UserType[]>(initialPeople) // need to fix any

    const finalPeople = people.map((p: UserType) => (
        <div className={s.peopleForHW8} key={p._id}>
            <span>{p.name}</span>
            <span>{p.age}</span>
        </div>
    ))

    const sortUp = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'up'}))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'down'}))
    const check18 = () => setPeople(homeWorkReducer(initialPeople, {type: 'check', payload: 18}))

    return (
        <div>
            <hr/>

            8

            <div className={s.HW8container}>
                {finalPeople}
            </div>

            <div className={s.buttonsHW8}>
                <>
                    <button onClick={sortUp}>SORT UP</button>
                </>
                <>
                    <button onClick={sortDown}>SORT DOWN</button>
                </>
                <>
                    <button onClick={check18}>CHECK 18</button>
                </>
            </div>

            <hr/>
            <hr/>
        </div>
    )
}

export default HW8
